<template>
  <div>
<button @click="goback()">电影页面的返回</button>
<go-back></go-back>
<to-search></to-search>

<ul>
  <li is='router-link' :to='"/movieDetail?id="+item.id' class="box" v-for='item in movies' :key="item.id">
<h3>
  影名：{{item.name}}

</h3><p>
  价格：{{item.price|filterPrice}}
</p>
<p>
  时间：{{item.time|filterTime}}
</p>
  </li>
</ul>
  </div>
</template>

<script>
import {reqMovieList} from "../../utils/http"
import axios from 'axios'
import {mapGetters,mapActions} from 'vuex'
export default {
  computed: {
    ...mapGetters([
    'movies',]
    ),
  },
  methods: {
    goback(){
      this.$router.go(-1)
    },
    ...mapActions([
      'reqMovie'
    ])
  },
  mounted() {
    this.reqMovie()
  },

}
</script>

<style scoped>
a{
  display: block;
}
</style>



